* {
  box-sizing: content-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  line-height: 1.4;
}

a{
  color: #333;
  text-decoration: none; 
}

.container{
  padding: 0 1rem;
}

.btn{
    display: inline-block;
    border: none;
    background: #555555;
    color: #fff;
    padding: 7px 20px;
    cursor: pointer;
}

.btn:hover{
  background: #666
}
